<template>
  <div class="login">
    <div class="bg"></div>
    <Modal class="login-modal" v-model="loginModal" width="360"
           class-name="vertical-center-modal" :closable="false" :mask-closable="false">
      <div>
        <h2 style="margin:10px;color:#abcdef;text-align:center">
          <img :src="require('@/assets/img/logo.png')" alt="" height="50">
        </h2>
        <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" >
          <Form-item prop="name">
            <Input v-model="formValidate.name" :placeholder="$t('userName')"></Input>
          </Form-item>
          <Form-item prop="password">
            <Input v-model="formValidate.password" @keyup.native.enter="handleSubmit('formValidate')" type="password" :placeholder="$t('password')"></Input>
          </Form-item>
          <Row>
            <Col span="12">
            <Checkbox-group v-model="formValidate.remember">
              <Checkbox :label="$t('remember')"></Checkbox>
            </Checkbox-group>
            </Col>
            <Col span="12">
            <!--<a style="float:right" @click="toRegister">新用户注册</a>-->
            </Col>
          </Row>

        </Form>
      </div>
      <div slot="footer">
        <Button type="primary" size="large" long :loading="modal_loading" @click="handleSubmit('formValidate')">{{ $t('login') }}</Button>
        <!--<small style="text-align: center;display:block" class="subText">@copyright by Artiely</small>-->
      </div>
    </Modal>
  </div>
</template>
<script>
  import Cookies from 'js-cookie'
  export default {
    name: 'login',
    data () {
      return {
        loginModal: true,
        modal_loading: false,
        formValidate: {
          name: '',
          password: ''
        },
        ruleValidate: {
          name: [
            {required: true, message: this.$t('userNameTip'), trigger: 'blur'}
          ],
          password: [
            {required: true, message: this.$t('passwordTip'), trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      handleSubmit (name) { // login
        this.$refs[name].validate((valid) => {
          this.modal_loading = true
          setTimeout(() => {
            this.modal_loading = false
            if (valid) {
              if ((this.formValidate.name === 'admin') && (this.formValidate.password === '123456')) {
                this.$Message.success(this.$t('loginSuccess'))
                Cookies.set('token', this.formValidate.password)
                this.$router.push('/index')
              } else {
                this.$Message.error(this.$t('loginFault'))
                this.$Notice.warning({
                  title: this.$t('loginTip'),
                  desc: this.$t('loginFaultTip')
                })
              }
            } else {
              this.$Notice.warning({
                title: this.$t('loginTip'),
                desc: this.$t('loginFaultTip')
              })
            }
          }, 2000)
        })
      }
//      toRegister () {
//        this.$router.push('/register')
//      }
    }
  }
</script>





